    monospace;
}


.header{
    position: fixed;
    top: 0;
    left: 0;
    padding: 10px 20px;
    width: 100%;
    line-height: 24px;
    font-size: 12px;
    vertical-align: middle;
}

.menu-wrapper{
    padding: 0;
    margin: 0;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;
}
.menu-content-wrapper{
    width: 25%;
    padding: 2px 5px;
    border-top: 1px solid #eee;
    position: relative;
}
.menu-release{
    text-align: center;
    border-radius: 50%;
    padding: 2px;
    border: 1px solid #eee;
}
.menu-content{
    padding: 2px;
    position: relative;
    height: 52px;
}
.release{
    position: absolute;
    bottom: -4px;
    width: 100%;
    text-align: center;
}
.release-box{
    width: 100%;
    height: 50px;
    position: absolute;
    bottom: 20px;
}
.release-box img{
    z-index: 300;
    background: #fff;
}


@font-face {
  font-family: 'icomoon';
  src:  url(/static/media/icomoon.04e9412d.eot);
  src:  url(/static/media/icomoon.04e9412d.eot#iefix) format('embedded-opentype'),
    url(/static/media/icomoon.a8fa6bed.ttf) format('truetype'),
    url(/static/media/icomoon.b05a54ef.woff) format('woff'),
    url(/static/media/icomoon.bdb41a58.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

